import React, { Component } from "react";
import MediaQuery from "react-responsive";
import { styled_section, styled_objectGroup, figureBG } from "../commonStyles";
import { statisticsData } from "../../../config";

const Statistics = styled_section();

const ObjectGroup = styled_objectGroup(`
  > div {
    background: ${figureBG.shadow};
    border-radius: 50%;
  }

  @media (min-width: 390px) {
    > div {
      width: 160px;
      height: 160px;
    }
  }
  @media (min-width: 750px) {
    margin: 50px 10px;
    > div {
      width: 260px;
      height: 260px;
    }
  }
`);

const Object = ({ data }) => (
  <div>
    <span font-size="medium">{data.key}</span>
    <div font-size="title">{data.value}</div>
  </div>
);

class Section extends Component {
  render() {
    return (
      <Statistics>
        <div font-size="title">동덕여자대학교</div>
        <div font-size="xlarge">게임 개발 동아리 NPC</div>
        <MediaQuery minWidth={1200}>
          <span font-size="medium">
            다양한 분야의 사람들과의 협업, 구글 플레이 출시, 스터디 등 다양한
            경험을 쌓을 수 있습니다.
          </span>
        </MediaQuery>
        <ObjectGroup>
          {statisticsData.map((data) => (
            <Object data={data} />
          ))}
        </ObjectGroup>
        <MediaQuery maxWidth={1200}>
          <span font-size="medium">
            다양한 분야의 사람들과의 협업, 구글 플레이 출시, 스터디 등 다양한
            경험을 쌓을 수 있습니다.
          </span>
        </MediaQuery>
      </Statistics>
    );
  }
}

export default Section;
