import React, { Component } from "react";
import MediaQuery from "react-responsive";
import { curriculumData } from "../../../config";
import { styled_objectGroup, styled_section, figureBG } from "../commonStyles";

const Curriculum = styled_section();

const ObjectGroup = styled_objectGroup(`
  > div {
    background: ${figureBG.shadow};
    border-radius: 10px;
    text-align: center;

    span {
      margin-top: 5px;
    }
  }

   @media (min-width: 390px) {
    > div {
      width: 100%;
      height: 120px;
    }
  }
  @media (min-width: 750px) {
    margin: 50px 10px;
    > div {
      width: 280px;
      height: 250px;
    }
  }
`);

const Object = ({ data }) => (
  <div>
    <div font-size="large">{data.key}</div>
    <span font-size="xsmall">{data.value}</span>
  </div>
);
class Section extends Component {
  render() {
    return (
      <Curriculum>
        <div font-size="xlarge">
          약 1년 간{" "}
          <MediaQuery maxWidth={750}>
            <br />
          </MediaQuery>
          어떤 활동을 하게되나요?
        </div>
        <span font-size="small">
          희망자에 따라 <b>MT, 스터디, 사이드 프로젝트</b> 등 다양한 활동들도
          함께할 수 있습니다
        </span>
        <ObjectGroup>
          {curriculumData.map((data) => (
            <Object data={data} />
          ))}
        </ObjectGroup>
      </Curriculum>
    );
  }
}

export default Section;
