import React, { Component } from "react";
import { styled_section } from "../commonStyles";

export const Title = styled_section(`
  > img { width: 100%; }

  @media (max-width: 750px) {
    width: 100%;
  }
`);

class Section extends Component {
  render() {
    return (
      <Title>
        <img src={require("../images/title.webp")} />
      </Title>
    );
  }
}

export default Section;
