import React, { Component } from "react";
import MediaQuery from "react-responsive";
import styled from "styled-components";
import {
  styled_section,
  styled_objectGroup,
  background,
  figureBG,
} from "../commonStyles";
import { juniorProjectData } from "../../../config";
import road from "../images/juniorProjectRoad.svg";

const JuniorProject = styled_section(`
  
`);

const ObjectGroup = styled_objectGroup(`
  position: relative;
  flex-direction: column;
  align-items: flex-start;
  align-content: center;

  > img {
    position: absolute;
    z-index: 1;
  }
  > div {
    position: relative;
    flex-direction: row;
    margin: 15px 0px;

    z-index: 2;

    > div:first-child {
      border-radius: 50%;
      margin-right: 20px;

      background: ${figureBG.point};
      box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);

      color: ${background};
      text-align: center;
    }

    > div:last-child {
      display: flex;
      flex-direction: column;
      justify-content: center;

      > div {
        margin-bottom: 5px;
      }
    }
  }
  @media (min-width: 390px) {
    > img {
      width: 36px;
      left: 45px;
    }
    > div {
      :nth-child(1), :nth-child(3) { left: -10px; }
      :nth-child(2), :nth-child(4) { left: 10px; }

      > div {
        :first-child {
          width: 38px;
          height: 38px;
          line-height: 38px;
        }
        :last-child {
          height: 65px;
        }
      }
    }
  }
  @media (min-width: 750px) {
    margin-top: 40px;
    > img {
      width: 50px;
      left: 190px;
    }
    > div {
      margin: 35px 0px;

      :nth-child(1), :nth-child(3) { left: -10px; }
      :nth-child(2), :nth-child(4) { left: 10px; }

      > div{
        :first-child {
          width: 60px;
          height: 60px;
          line-height: 60px;
          margin-right: 50px;
        }
        :last-child {
          height: 72px;
        }
      }
    }
  }
  @media (min-width: 1200px) {
    margin-top: 50px;
    > img {
      width: 50px;
      left: 415px;
    }
    > div {
      margin: 35px 0px;

      :nth-child(1), :nth-child(3) { left: -10px; }
      :nth-child(2), :nth-child(4) { left: 10px; }

      > div{
        :first-child {
          width: 60px;
          height: 60px;
          line-height: 60px;
          margin-right: 50px;
        }
        :last-child {
          height: 72px;
        }
      }
    }
  }
`);

const Object = ({ idx, data }) => (
  <div>
    <div font-size="large">0{idx + 1}</div>
    <div>
      <div font-size="large">{data.key}</div>
      <span font-size="xsmall">{data.value}</span>
    </div>
  </div>
);
class Section extends Component {
  render() {
    return (
      <JuniorProject>
        <div font-size="xlarge">
          신입부원 프로젝트는 어떻게&nbsp;
          <MediaQuery maxWidth={750}>
            <br />
          </MediaQuery>
          진행되나요?
        </div>
        <ObjectGroup>
          {juniorProjectData.map((data, idx) => (
            <Object data={data} idx={idx} />
          ))}
          <img src={road} />
        </ObjectGroup>
      </JuniorProject>
    );
  }
}

export default Section;
